body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  position: relative;
}

.portal {
    height: 200px;
    width: 100vw;
    background: white;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 3;
    padding: 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-shadow: -2px -2px 9px 0px rgba(50, 50, 50, 0.45);
            box-shadow: -2px -2px 9px 0px rgba(50, 50, 50, 0.45);
}

.all_markers {
    width: 250px;
    border-radius: 5px;
    background: white;
    min-height: 600px;
    position: absolute;
    left: 20px;
    top: 20px;
    z-index: 3;
    padding: 15px;
}

.marker {
    background: grey;
    margin-bottom: 10px;
    width: 100%;
    height: 50px;
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    color: white;
    -webkit-box-shadow: 3px 3px 5px 0px rgba(50, 50, 50, 0.75);
            box-shadow: 3px 3px 5px 0px rgba(50, 50, 50, 0.75);
}

.close {
    position: absolute;
    right: 20px;
    top: 10px;
    font-size: 30px;
    cursor: pointer;
}

.create_new {
    width: 250px;
    border-radius: 5px;
    background: white;
    min-height: 600px;
    position: absolute;
    right: 20px;
    top: 20px;
    z-index: 3;
    padding: 15px;
}

h5 {
    margin: 15px 0;
}

input {
    width: 100%;
    height: 20px;
}

button {
    display: block;
    width: 100px;
    height: 40px;
    margin: 15px auto;
}



